<template>
  <v-card rounded="xl" class="campaign">
    <v-card-actions flat height="fit-content" class="pa-0">
      <v-row no-gutters class="text-center text-uppercase campaign_toolbar">
        <v-col cols="6" class="pa-0">
          <v-sheet
            shaped
            dark
            color="purple"
            height="100%"
            width="100%"
            class="d-flex justify-center align-center"
          >
            <h4>Spent $ {{ campaign_spendings.spent }}</h4>
          </v-sheet>
        </v-col>
        <v-col cols="6" class="text-uppercase d-flex justify-center align-center px-2 campaign_name">
          <h4>{{ campaign_spendings.name.length>45?campaign_spendings.name.slice(0,45)+"...":campaignName }}</h4>
        </v-col>
      </v-row>
    </v-card-actions>

    <v-card-text>
      <v-row>
        <v-col
          cols="4"
          v-for="(kpi, index) in campaign_spendings.KPI"
          :key="'feature' + index"
          :set="
            ((isCurrency = !isPercentage(kpi.name)),
            (delta = deltaSpendings(kpi)))
          "
        >
          <v-card class="campaign_spendings" rounded="lg">
            <v-row no-gutters class="kpi_header">
              <v-col cols="6">
                <div class="text-sm-center mt-0">
                  7 days {{ campaign_spendings.spent }}
                </div>
              </v-col>
              <v-col cols="6" :delta="delta">
                <v-sheet
                  :color="delta < 0 ? 'red' : 'green'"
                  height="100%"
                  width="100%"
                  class="d-flex justify-center align-center mt-0 rounded-tr-lg"
                >
                  {{ delta }}%
                </v-sheet>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12">
                <div class="align-center kpi_name">{{ kpi.name }}</div>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="12">
                <div v-if="isCurrency" class="kpi_value">
                  {{ currency(kpi.value) }}
                </div>
                <div v-else class="kpi_value">{{ kpi.value }}</div>
              </v-col>
            </v-row>
            <div class="card__footer_gradient"></div>
          </v-card>
        </v-col>
      </v-row>
    </v-card-text>

    <v-card-actions class="mx-0 mb-0 mt-4 pa-0">
      <v-row no-gutters>
        <v-col cols="6">
          <v-btn
            block
            dark
            color="#009788"
            class="py-8"
            style="border-radius: 0 0 0 24px"
          >
            Go to Campaign <v-icon large class="ml-6">mdi-arrow-right</v-icon>
          </v-btn>
        </v-col>

        <v-col cols="6" class="d-flex justify-center align-center">
          Quick View
        </v-col>
      </v-row>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  name: "AdAccountsKpiOverview",
  props: ["campaignName"],
  methods: {
    isPercentage(kpiName) {
      return ["Conversion", "CTR"].includes(kpiName);
    },
    currency: (str, currency = "USD") => {
      // strip currency Symbols $ € £
      let currencyValue = parseFloat(str.replace(/£|\$|€/g, ""));
      if (currencyValue)
        return new Intl.NumberFormat("en-US", {
          style: "currency",
          currency: currency,
          minimumFractionDigits: 2,
        }).format(currencyValue);
      else return currencyValue;
    },
    deltaSpendings: (kpi) => {
      let number = (x) => parseFloat(x.replace(/£|\$|€/g, ""));
      let previous = number(kpi.previous);
      let value = number(kpi.value);
      let delta = value - previous;
      let percent = delta / (previous / 100);
      percent = parseFloat(percent).toFixed(2);
      //console.log(kpi.name, previous, value, percent);
      return parseFloat(percent, 2);
    },
  },
  created() {
    //console.log(this.campaign_spendings)
  },
  data() {
    return {
      // Better to move to store ???
      campaign_spendings: {
        name: "Campaign Name 1",
        spent: "8.20",
        KPI: [
          {
            name: "CPA",
            previous: "8",
            value: "$ 8.37",
            // calculate percentage once here
          },
          {
            name: "Conversion",
            previous: "0.40",
            value: "0.50",
          },
          {
            name: "ROAS",
            previous: "1",
            value: "1.2",
          },
          {
            name: "CPM",
            previous: "5.50",
            value: "€ 5.00",
          },
          {
            name: "CPC",
            previous: "0.60",
            value: "0.50",
          },
          {
            name: "CTR",
            previous: "0.60",
            value: "0.50",
          },
        ],
      },
    };
  },
};
</script>

<style scoped lang="scss">
.campaign {
  --KPIHeight: 12em;
  --HeaderHeight: calc(var(--KPIHeight)/2.3);
  --KPINameMargin: 0.5em;
  --KPIValueFontSize: 150%;
  --FooterHeight: 1em;
  --RBSgreen: #009788;
  --RBSwhite: white;
  --RBSpurple: purple;
  --DEVcolor: hotpink;
}
.campaign_name {
  height: var(--HeaderHeight);
}
.campaign_spendings {
  height: var(--KPIHeight);
  position: relative;
}
.campaign_spendings .col-6 {
  border-bottom: .1em solid grey;
}
.card__footer_gradient {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: var(--FooterHeight);
  background-image: linear-gradient(
    to right,
    var(--RBSgreen),
    var(--RBSpurple)
  );
}
.kpi_name,
.kpi_value {
  text-align: center;
  font-weight: bold;
  color: var(--RBSpurple);
}
.kpi_name {
  border: .2em solid var(--RBSpurple);
  margin: 0 var(--KPINameMargin);
}
.kpi_value {
  font-size: var(--KPIValueFontSize);
}
</style>
